.page-main {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  font-family: @font-main;
  max-width: 1110px;
}

.page-main__pagination {
  margin-top: 50px;
  display: flex;
  align-self: flex-start;
  flex-wrap: wrap;

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
  }
}

.page-main__pagination-link {
  &:nth-of-type(n+2) {
    margin-left: 26px;
  }

  a {
    color: @basic-grey;
    cursor: pointer;
    position: relative;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    text-decoration: none;
    transition: color @trans--300;

    &:hover {
      color: @basic-black;
    }

    &::before {
      background-color: @basic-grey;
      content: "";
      position: absolute;
      width: 1px;
      height: 15px;
      top: 4px;
      right: -10px;
      transform: rotate(20deg);
    }
  }

  &--inactive {
    a {
      color: @basic-black;
      cursor: default;

      &::before {
        display: none;
      }
    }
  }
}

@media (max-width: @mobile-width) {
  .page-main {
    padding-left: 16px;
    padding-right: 16px;
    max-width: 650px;
  }

  .page-main__pagination {
    margin-top: 0;
  }
}
