.pages {
  align-self: center;
}

.pages__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  align-items: flex-end;
}

.pages__item:not(.pages__item--logo) {
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;

  a {
    text-decoration: none;
    color: @basic-grey;
    transition: color @trans--300;

    &:hover {
      color: @basic-black;
    }
  }

  &:nth-of-type(n+2) {
    margin-left: 30px;
  }
}

.pages__item--logo {
  margin-left: 64px;
  margin-right: 34px;
}

@media (max-width: @mobile-width) {
  .pages {
    align-self: center;
    display: none;

    &--opened {
      display: block;
    }
  }

  .pages__list {
    align-items: center;
    padding: 15px 10px 15px 10px;
    border-top: 1px solid @basic-grey;
    border-bottom: 1px solid @basic-grey;
    margin-bottom: 20px;
  }

  .pages__item:not(.pages__item--logo) {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    min-width: 320px;
    width: 100%;
    text-align: center;
  
    &:nth-of-type(n+2) {
      margin-left: 0;
      margin-top: 25px;
      border-top: 1px solid @basic-grey;
      padding-top: 20px;
    }
  }

  .pages__item--logo {
    display: none;
  }
}
