.choice {
  position: relative;
  align-self: center;
  width: 100%;
  max-width: 505px;
  height: 46px;
  box-sizing: border-box;
  margin-bottom: 20px;

  &[data-state="active"] {
    .choice__title {
      &::before {
        transform: translate(-3px, -50%) rotate(-180deg);
      }
    }
    
    .choice__content {
      display: flex;
    }
    
    .choice__label + .choice__input + .choice__label {
      max-height: 40px;
      border-top-width: 1px;
    }
  }
}

.choice__title {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 18px 9px 9px 0;
  border-bottom: solid 1px @basic-grey;
  cursor: pointer;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: @basic-grey;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 6px;
    display: block;
    width: 12px;
    height: 13px;
    transition: all 0.3s ease-out;
    background-image: url("../img/icon-arrow.svg");
    background-size: 13px 10px;
    background-repeat: no-repeat;
  }
}

.choice__content {
  position: absolute;
  top: 46px;
  left: 0;
  box-sizing: border-box;
  flex-direction: column;
  width: 100%;
  background-color: @basic-white;
  border: 1px solid @basic-grey;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: all 0.3s ease-out;
  display: none;
  z-index: 8;
  padding: 20px 20px 19px 20px;
}

.choice__input {
  visibility: hidden;
  width: 0.1px;
  height: 0.1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  box-sizing: border-box;

  &:checked + label {
    color: @basic-black;
  }
  &:disabled + label {
    opacity: 0.6;
    pointer-events: none;
  }
}

.choice__label {
  display: flex;
  align-items: center;
  width: 100%;
  max-height: 0;
  box-sizing: border-box;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: @basic-grey;
  transition: all 0.2s ease-out;
  cursor: pointer;
  overflow: hidden;

  &:nth-of-type(n+3) {
    margin-top: 19px;
  }

  & + input + & {
    color: @basic-grey;

    &:hover {
      color: @basic-black;
    }
  }
}

@media (max-width: @mobile-width) {
  .choice {
    max-width: 100%;
  }
}