.personal {
  align-self: center;
  display: flex;
  margin-top: 50px;
  margin-bottom: 70px;
  justify-content: center;
  width: 100%;
  font-family: @font-main;
}

.personal__products {
  margin-right: 50px;
  width: 100%;
}

.personal__products-info {
  h2 {
    font-weight: 700;
    font-size: 40px;
    line-height: 46px;
    color: @basic-black;
    margin: 0;
  }

  p {
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: @basic-black;
    margin: 0;
    margin-top: 15px;
  }

  a {
    color: @basic-grey;
    transition: color @trans--300;

    &:hover {
      color: @basic-black;
    }
  }
}

.personal__products-map {
  display: none;
}

.personal__products-address {
  align-self: center;
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  margin-bottom: 70px;

  form {
    align-self: flex-start;
    max-width: 555px;
    min-width: 505px;
  
    fieldset {
      margin: 0;
      padding: 0;
      display: flex;
      flex-wrap: wrap;
      border: none;
      max-width: 100%;
      box-sizing: border-box;
    }
  
    input:not(.choice__input) {
      height: 46px;
      border: none;
      border-bottom: 1px solid @basic-grey;
      padding: 9px;
      padding-left: 0;
      box-sizing: border-box;
      color: @basic-black;
      font-weight: 400;
      font-size: 16px;
      line-height: 120%;
      min-width: 505px;
      font-family: @font-main;
  
      &:nth-of-type(n+1) {
        margin-bottom: 20px;
      }
  
      &::placeholder {
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: @basic-grey;
      }

      &:invalid {
        color: @error-info;
        border-color: @error-info;
      }

      &:focus-visible {
        outline: none;
      }
    }

    input[name="tel"], input[name="name"] {
      min-width: 235px;
    }

    input[name="tel"] {
      margin-left: 35px;
    }
  
    textarea {
      height: 46px;
      border: none;
      padding: 9px;
      padding-left: 0;
      border-bottom: 1px solid @basic-grey;
      box-sizing: border-box;
      color: @basic-black;
      font-weight: 400;
      font-size: 16px;
      line-height: 120%;
      max-width: 505px;
      min-width: 505px;
      min-height: 46px;
  
      &::placeholder {
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: @basic-grey;
        font-family: @font-main;
      }
    }
  }
}

.personal__map {
  display: flex;
  flex-direction: column;
  position: relative;
  max-height: 700px;
  width: 555px;
}

.personal__image {
  width: 555px;
  height: 700px;
  margin: 0;
  padding: 0;

  iframe {
    border: none;
  }
}

.personal__map-total {
  display: flex;
  width: 100%;
  height: 136px;
  background-color: @total-info;
  box-sizing: border-box;
  justify-content: space-between;
  padding-left: 30px;
  padding-right: 30px;
  position: absolute;
  bottom: 0;
  left: 0;

  p {
    font-weight: 700;
    font-size: 40px;
    line-height: 46px;
    color: @basic-white;
    margin: 0;
    align-self: center;
  }
}

@media (max-width: @mobile-width) {
  .personal {
    flex-direction: column;
    margin-top: 32px;
    margin-bottom: 40px;
  }
  
  .personal__products {
    margin-right: 0;
    align-self: center;
  }
  
  .personal__products-info {
    h2 {
      font-size: 30px;
      line-height: 34px;
    }
  
    p {
      margin-top: 12px;
    }
  }

  .personal__products-map {
    display: block;
    width: 100%;

    iframe {
      border: none;
      width: 100%;
    }
  }

  .personal__products-address {
    margin-top: 24px;
    margin-bottom: 40px;
  
    form {
      align-self: center;
      min-width: 287px;
      width: 100%;
      max-width: 100%;

      fieldset {
        width: 100%;
      }
    
      input:not(.choice__input) {
        padding: 18px 9px 9px 0;
        min-width: 287px;
        width: 100%;
    
        &:nth-of-type(n+1) {
          margin-bottom: 16px;
        }
      }

      input[name="tel"], input[name="name"] {
        min-width: 287px;
      }
  
      input[name="tel"] {
        margin-left: 0;
      }
    
      textarea {
        padding: 18px 9px 9px 0;
        min-width: 287px;
        width: 100%;
        max-width: 100%;
      }
    }
  }

  .personal__map {
    display: none;
  }
}
