.page-header {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding-top: 30px;
  margin: 0 auto;
  width: 100%;
  align-self: center;
  max-width: 1110px;
  margin-bottom: 40px;
  font-family: @font-main;
}

.page-header__button {
  display: none;
  margin-right: 16px;
}

.page-header__nav-logo {
  display: none;
}

.page-header__nav {
  display: flex;
  width: 100%;
  align-self: center;
  justify-content: space-between;
}

.page-header__nav-search {
  display: flex;

  button {
    border: none;
    background-color: transparent;
    margin: 0;
    padding: 0;
    background-image: url("../img/icon-search-dt.svg");
    background-size: 25px 25px;
    width: 25px;
    height: 25px;
    margin-right: 20px;
  }
}

.page-header__nav-input {
  border: none;
  border-bottom: 1px solid @basic-grey;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: @basic-black;
  font-family: @font-main;

  &::placeholder {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: @basic-grey;
  }
}

.page-header__nav-user-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;

  li {
    &:nth-of-type(n+2) {
      margin-left: 13px;
    }
  }
}

.page-header__nav-user-menu-link {
  text-decoration: none;
  background-repeat: no-repeat;

  &--profile {
    background-image: url("../img/icon-profile-dt.svg");
    background-size: 22px 24px;
    width: 22px;
    height: 25px;
    margin-top: 2px;
  }

  &--favorite {
    background-image: url("../img/icon-heart-dt.svg");
    background-size: 24px 22px;
    width: 24px;
    height: 22px;
    margin-top: 4px;
  }

  &--cart {
    background-image: url("../img/icon-cart-dt.svg");
    background-size: 31px 26px;
    width: 31px;
    height: 26px;
  }
}

.page-header__nav-search-menu {
  display: flex;
}

@media (max-width: @mobile-width) {
  .page-header {
    padding-top: 20px;
    max-width: 100%;
    margin-bottom: 41px;
    padding-left: 16px;
    padding-right: 16px;
    max-width: 650px;
  }

  .page-header__button {
    display: block;
    border: none;
    background-color: transparent;
    padding: 0;
    background-image: url("../img/icon-menu-closed.svg");
    background-size: 28px 20px;
    background-repeat: no-repeat;
    width: 28px;
    height: 20px;
    margin-top: 2px;

    &--opened {
      background-image: url("../img/icon-cross.svg");
      background-size: 20px 20px;
    }
  }

  .page-header__nav-logo {
    display: block;
  }
  
  .page-header__nav-search {
    button {
      background-size: 22px 22px;
      width: 22px;
      height: 22px;
      margin-right: 0;
    }
  }

  .page-header__nav-input {
    display: none;

    &--opened {
      display: block;
    }
  }

  .page-header__nav-user-menu {  
    li {
      &:nth-of-type(n+2) {
        margin-left: 16px;
      }
    }
  }
  
  .page-header__nav-user-menu-link {  
    &--profile {
      display: none;
    }
  }
}
