@font-face {
  font-family: "Helvetica";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/helvetica_regular.woff") format("woff"), url("../fonts/helvetica_regular.woff2") format("woff2");
}
@font-face {
  font-family: "Helvetica";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/helvetica_bold.woff") format("woff"), url("../fonts/helvetica_bold.woff2") format("woff2");
}
.page-main {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  font-family: "Helvetica", "Arial Black", sans-serif;
  max-width: 1110px;
}
.page-main__pagination {
  margin-top: 50px;
  display: flex;
  align-self: flex-start;
  flex-wrap: wrap;
}
.page-main__pagination ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.page-main__pagination-link:nth-of-type(n+2) {
  margin-left: 26px;
}
.page-main__pagination-link a {
  color: #9f9f9f;
  cursor: pointer;
  position: relative;
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
  text-decoration: none;
  transition: color 0.3s ease;
}
.page-main__pagination-link a:hover {
  color: #000000;
}
.page-main__pagination-link a::before {
  background-color: #9f9f9f;
  content: "";
  position: absolute;
  width: 1px;
  height: 15px;
  top: 4px;
  right: -10px;
  transform: rotate(20deg);
}
.page-main__pagination-link--inactive a {
  color: #000000;
  cursor: default;
}
.page-main__pagination-link--inactive a::before {
  display: none;
}
@media (max-width: 1110px) {
  .page-main {
    padding-left: 16px;
    padding-right: 16px;
    max-width: 650px;
  }
  .page-main__pagination {
    margin-top: 0;
  }
}
.page-header {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding-top: 30px;
  margin: 0 auto;
  width: 100%;
  align-self: center;
  max-width: 1110px;
  margin-bottom: 40px;
  font-family: "Helvetica", "Arial Black", sans-serif;
}
.page-header__button {
  display: none;
  margin-right: 16px;
}
.page-header__nav-logo {
  display: none;
}
.page-header__nav {
  display: flex;
  width: 100%;
  align-self: center;
  justify-content: space-between;
}
.page-header__nav-search {
  display: flex;
}
.page-header__nav-search button {
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  background-image: url("../img/icon-search-dt.svg");
  background-size: 25px 25px;
  width: 25px;
  height: 25px;
  margin-right: 20px;
}
.page-header__nav-input {
  border: none;
  border-bottom: 1px solid #9f9f9f;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  font-family: "Helvetica", "Arial Black", sans-serif;
}
.page-header__nav-input::placeholder {
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #9f9f9f;
}
.page-header__nav-user-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.page-header__nav-user-menu li:nth-of-type(n+2) {
  margin-left: 13px;
}
.page-header__nav-user-menu-link {
  text-decoration: none;
  background-repeat: no-repeat;
}
.page-header__nav-user-menu-link--profile {
  background-image: url("../img/icon-profile-dt.svg");
  background-size: 22px 24px;
  width: 22px;
  height: 25px;
  margin-top: 2px;
}
.page-header__nav-user-menu-link--favorite {
  background-image: url("../img/icon-heart-dt.svg");
  background-size: 24px 22px;
  width: 24px;
  height: 22px;
  margin-top: 4px;
}
.page-header__nav-user-menu-link--cart {
  background-image: url("../img/icon-cart-dt.svg");
  background-size: 31px 26px;
  width: 31px;
  height: 26px;
}
.page-header__nav-search-menu {
  display: flex;
}
@media (max-width: 1110px) {
  .page-header {
    padding-top: 20px;
    max-width: 100%;
    margin-bottom: 41px;
    padding-left: 16px;
    padding-right: 16px;
    max-width: 650px;
  }
  .page-header__button {
    display: block;
    border: none;
    background-color: transparent;
    padding: 0;
    background-image: url("../img/icon-menu-closed.svg");
    background-size: 28px 20px;
    background-repeat: no-repeat;
    width: 28px;
    height: 20px;
    margin-top: 2px;
  }
  .page-header__button--opened {
    background-image: url("../img/icon-cross.svg");
    background-size: 20px 20px;
  }
  .page-header__nav-logo {
    display: block;
  }
  .page-header__nav-search button {
    background-size: 22px 22px;
    width: 22px;
    height: 22px;
    margin-right: 0;
  }
  .page-header__nav-input {
    display: none;
  }
  .page-header__nav-input--opened {
    display: block;
  }
  .page-header__nav-user-menu li:nth-of-type(n+2) {
    margin-left: 16px;
  }
  .page-header__nav-user-menu-link--profile {
    display: none;
  }
}
body {
  margin: 0;
  font-family: "Helvetica", "Arial Black", sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip-path: rect(1px, 1px, 1px, 1px);
}
.personal {
  align-self: center;
  display: flex;
  margin-top: 50px;
  margin-bottom: 70px;
  justify-content: center;
  width: 100%;
  font-family: "Helvetica", "Arial Black", sans-serif;
}
.personal__products {
  margin-right: 50px;
  width: 100%;
}
.personal__products-info h2 {
  font-weight: 700;
  font-size: 40px;
  line-height: 46px;
  color: #000000;
  margin: 0;
}
.personal__products-info p {
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #000000;
  margin: 0;
  margin-top: 15px;
}
.personal__products-info a {
  color: #9f9f9f;
  transition: color 0.3s ease;
}
.personal__products-info a:hover {
  color: #000000;
}
.personal__products-map {
  display: none;
}
.personal__products-address {
  align-self: center;
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  margin-bottom: 70px;
}
.personal__products-address form {
  align-self: flex-start;
  max-width: 555px;
  min-width: 505px;
}
.personal__products-address form fieldset {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  border: none;
  max-width: 100%;
  box-sizing: border-box;
}
.personal__products-address form input:not(.choice__input) {
  height: 46px;
  border: none;
  border-bottom: 1px solid #9f9f9f;
  padding: 9px;
  padding-left: 0;
  box-sizing: border-box;
  color: #000000;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  min-width: 505px;
  font-family: "Helvetica", "Arial Black", sans-serif;
}
.personal__products-address form input:not(.choice__input):nth-of-type(n+1) {
  margin-bottom: 20px;
}
.personal__products-address form input:not(.choice__input)::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #9f9f9f;
}
.personal__products-address form input:not(.choice__input):invalid {
  color: #d80909;
  border-color: #d80909;
}
.personal__products-address form input:not(.choice__input):focus-visible {
  outline: none;
}
.personal__products-address form input[name="tel"],
.personal__products-address form input[name="name"] {
  min-width: 235px;
}
.personal__products-address form input[name="tel"] {
  margin-left: 35px;
}
.personal__products-address form textarea {
  height: 46px;
  border: none;
  padding: 9px;
  padding-left: 0;
  border-bottom: 1px solid #9f9f9f;
  box-sizing: border-box;
  color: #000000;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  max-width: 505px;
  min-width: 505px;
  min-height: 46px;
}
.personal__products-address form textarea::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #9f9f9f;
  font-family: "Helvetica", "Arial Black", sans-serif;
}
.personal__map {
  display: flex;
  flex-direction: column;
  position: relative;
  max-height: 700px;
  width: 555px;
}
.personal__image {
  width: 555px;
  height: 700px;
  margin: 0;
  padding: 0;
}
.personal__image iframe {
  border: none;
}
.personal__map-total {
  display: flex;
  width: 100%;
  height: 136px;
  background-color: rgba(0, 0, 0, 0.8);
  box-sizing: border-box;
  justify-content: space-between;
  padding-left: 30px;
  padding-right: 30px;
  position: absolute;
  bottom: 0;
  left: 0;
}
.personal__map-total p {
  font-weight: 700;
  font-size: 40px;
  line-height: 46px;
  color: #ffffff;
  margin: 0;
  align-self: center;
}
@media (max-width: 1110px) {
  .personal {
    flex-direction: column;
    margin-top: 32px;
    margin-bottom: 40px;
  }
  .personal__products {
    margin-right: 0;
    align-self: center;
  }
  .personal__products-info h2 {
    font-size: 30px;
    line-height: 34px;
  }
  .personal__products-info p {
    margin-top: 12px;
  }
  .personal__products-map {
    display: block;
    width: 100%;
  }
  .personal__products-map iframe {
    border: none;
    width: 100%;
  }
  .personal__products-address {
    margin-top: 24px;
    margin-bottom: 40px;
  }
  .personal__products-address form {
    align-self: center;
    min-width: 287px;
    width: 100%;
    max-width: 100%;
  }
  .personal__products-address form fieldset {
    width: 100%;
  }
  .personal__products-address form input:not(.choice__input) {
    padding: 18px 9px 9px 0;
    min-width: 287px;
    width: 100%;
  }
  .personal__products-address form input:not(.choice__input):nth-of-type(n+1) {
    margin-bottom: 16px;
  }
  .personal__products-address form input[name="tel"],
  .personal__products-address form input[name="name"] {
    min-width: 287px;
  }
  .personal__products-address form input[name="tel"] {
    margin-left: 0;
  }
  .personal__products-address form textarea {
    padding: 18px 9px 9px 0;
    min-width: 287px;
    width: 100%;
    max-width: 100%;
  }
  .personal__map {
    display: none;
  }
}
.goods {
  display: flex;
  flex-direction: column;
}
.goods h4 {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  color: #000000;
  margin: 0;
}
.goods__list ul {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}
.goods__list li {
  display: flex;
  min-height: 150px;
  max-width: 506px;
  box-sizing: border-box;
  padding-bottom: 25px;
  padding-right: 35px;
  justify-content: space-between;
}
.goods__list li:nth-of-type(n+2) {
  border-top: 1px solid #dedede;
  padding-top: 25px;
}
.goods__list-description {
  display: flex;
  flex-direction: column;
  margin-right: 29px;
  justify-content: space-between;
}
.goods__list-description h5 {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  color: #000000;
  margin: 0;
}
.goods__list-description p {
  margin-top: 10px;
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #9f9f9f;
  max-width: 155px;
}
.goods__list-description div {
  box-sizing: border-box;
  border: 1px solid #000000;
  width: 114px;
  height: 40px;
  display: flex;
  justify-content: space-between;
}
.goods__list-description div span {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  color: #000000;
  align-self: center;
}
.goods__list-description-button {
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  width: 38px;
  height: 40px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.goods__list-description-button--minus {
  background-image: url("../img/icon-minus-dt.svg");
}
.goods__list-description-button--plus {
  background-image: url("../img/icon-plus-dt.svg");
}
.goods__list-price {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.goods__list-price p {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  color: #000000;
  text-align: right;
  align-self: flex-end;
  min-width: 92px;
}
.goods__list-price button {
  border: none;
  background-color: transparent;
  text-decoration: underline;
  margin: 0;
  padding: 0;
  width: 56px;
  height: 20px;
  text-align: right;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #9f9f9f;
  align-self: flex-end;
  cursor: pointer;
  transition: color 0.3s ease;
}
.goods__list-price button:hover {
  color: #000000;
}
.goods__list-buy-button {
  padding: 24px 216px 23px 217px;
  box-sizing: border-box;
  width: 505px;
  height: 70px;
  background-color: #ffffff;
  border: 1px solid #000000;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: #000000;
  cursor: pointer;
  transition: color 0.3s ease;
  transition: background-color 0.3s ease;
}
.goods__list-buy-button:hover {
  color: #ffffff;
  background-color: #000000;
}
.goods__list-buy-button:active {
  color: #ffffff;
  background-color: #000000;
}
.goods__list-total {
  display: none;
}
@media (max-width: 1110px) {
  .goods h4 {
    font-size: 26px;
    line-height: 30px;
  }
  .goods__list {
    display: flex;
    flex-direction: column;
  }
  .goods__list ul {
    margin-top: 32px;
  }
  .goods__list li {
    align-self: center;
    min-height: 80px;
    min-width: 288px;
    padding-bottom: 16px;
    padding-right: 0;
    width: 100%;
    max-width: 100%;
  }
  .goods__list li:nth-of-type(n+2) {
    padding-top: 16px;
  }
  .goods__list li img {
    width: 80px;
    height: 80px;
  }
  .goods__list-description {
    margin-right: 16px;
  }
  .goods__list-description h5 {
    font-size: 16px;
    line-height: 18px;
  }
  .goods__list-description p {
    display: none;
  }
  .goods__list-description div {
    width: 81px;
    height: 34px;
  }
  .goods__list-description div span {
    font-size: 16px;
    line-height: 18px;
  }
  .goods__list-description-button {
    width: 27px;
    height: 34px;
  }
  .goods__list-price p {
    font-size: 16px;
    line-height: 18px;
    min-width: 73px;
  }
  .goods__list-price button {
    width: 48px;
    height: 14px;
    font-size: 12px;
    line-height: 14px;
  }
  .goods__list-buy-button {
    padding: 19px 108px 18px 108px;
    min-width: 288px;
    width: 100%;
    height: 60px;
    align-self: center;
    max-width: 505px;
  }
  .goods__list-total {
    display: flex;
    width: 100%;
    align-self: center;
    box-sizing: border-box;
    justify-content: space-between;
    margin-top: 24px;
    margin-bottom: 24px;
  }
  .goods__list-total p {
    font-weight: 700;
    font-size: 26px;
    line-height: 30px;
    color: #000000;
    margin: 0;
    align-self: center;
  }
}
.choice {
  position: relative;
  align-self: center;
  width: 100%;
  max-width: 505px;
  height: 46px;
  box-sizing: border-box;
  margin-bottom: 20px;
}
.choice[data-state="active"] .choice__title::before {
  transform: translate(-3px, -50%) rotate(-180deg);
}
.choice[data-state="active"] .choice__content {
  display: flex;
}
.choice[data-state="active"] .choice__label + .choice__input + .choice__label {
  max-height: 40px;
  border-top-width: 1px;
}
.choice__title {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 18px 9px 9px 0;
  border-bottom: solid 1px #9f9f9f;
  cursor: pointer;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #9f9f9f;
}
.choice__title::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 6px;
  display: block;
  width: 12px;
  height: 13px;
  transition: all 0.3s ease-out;
  background-image: url("../img/icon-arrow.svg");
  background-size: 13px 10px;
  background-repeat: no-repeat;
}
.choice__content {
  position: absolute;
  top: 46px;
  left: 0;
  box-sizing: border-box;
  flex-direction: column;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #9f9f9f;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: all 0.3s ease-out;
  display: none;
  z-index: 8;
  padding: 20px 20px 19px 20px;
}
.choice__input {
  visibility: hidden;
  width: 0.1px;
  height: 0.1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  box-sizing: border-box;
}
.choice__input:checked + label {
  color: #000000;
}
.choice__input:disabled + label {
  opacity: 0.6;
  pointer-events: none;
}
.choice__label {
  display: flex;
  align-items: center;
  width: 100%;
  max-height: 0;
  box-sizing: border-box;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #9f9f9f;
  transition: all 0.2s ease-out;
  cursor: pointer;
  overflow: hidden;
}
.choice__label:nth-of-type(n+3) {
  margin-top: 19px;
}
.choice__label + input + .choice__label {
  color: #9f9f9f;
}
.choice__label + input + .choice__label:hover {
  color: #000000;
}
@media (max-width: 1110px) {
  .choice {
    max-width: 100%;
  }
}
.pages {
  align-self: center;
}
.pages__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  align-items: flex-end;
}
.pages__item:not(.pages__item--logo) {
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
}
.pages__item:not(.pages__item--logo) a {
  text-decoration: none;
  color: #9f9f9f;
  transition: color 0.3s ease;
}
.pages__item:not(.pages__item--logo) a:hover {
  color: #000000;
}
.pages__item:not(.pages__item--logo):nth-of-type(n+2) {
  margin-left: 30px;
}
.pages__item--logo {
  margin-left: 64px;
  margin-right: 34px;
}
@media (max-width: 1110px) {
  .pages {
    align-self: center;
    display: none;
  }
  .pages--opened {
    display: block;
  }
  .pages__list {
    align-items: center;
    padding: 15px 10px 15px 10px;
    border-top: 1px solid #9f9f9f;
    border-bottom: 1px solid #9f9f9f;
    margin-bottom: 20px;
  }
  .pages__item:not(.pages__item--logo) {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    min-width: 320px;
    width: 100%;
    text-align: center;
  }
  .pages__item:not(.pages__item--logo):nth-of-type(n+2) {
    margin-left: 0;
    margin-top: 25px;
    border-top: 1px solid #9f9f9f;
    padding-top: 20px;
  }
  .pages__item--logo {
    display: none;
  }
}
