.goods {
  display: flex;
  flex-direction: column;

  h4 {
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;
    color: @basic-black;
    margin: 0;
  }
}

.goods__list {
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
  }

  li {
    display: flex;
    min-height: 150px;
    max-width: 506px;
    box-sizing: border-box;
    padding-bottom: 25px;
    padding-right: 35px;
    justify-content: space-between;

    &:nth-of-type(n+2) {
      border-top: 1px solid @basic-light-grey;
      padding-top: 25px;
    }
  }
}

.goods__list-description {
  display: flex;
  flex-direction: column;
  margin-right: 29px;
  justify-content: space-between;

  h5 {
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    color: @basic-black;
    margin: 0;
  }

  p {
    margin-top: 10px;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: @basic-grey;
    max-width: 155px;
  }

  div {
    box-sizing: border-box;
    border: 1px solid @basic-black;
    width: 114px;
    height: 40px;
    display: flex;
    justify-content: space-between;

    span {
      font-weight: 700;
      font-size: 20px;
      line-height: 23px;
      color: @basic-black;
      align-self: center;
    }
  }
}

.goods__list-description-button {
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  width: 38px;
  height: 40px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  &--minus {
    background-image: url("../img/icon-minus-dt.svg");
  }

  &--plus {
    background-image: url("../img/icon-plus-dt.svg");
  }
}

.goods__list-price {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  p {
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    color: @basic-black;
    text-align: right;
    align-self: flex-end;
    min-width: 92px;
  }

  button {
    border: none;
    background-color: transparent;
    text-decoration: underline;
    margin: 0;
    padding: 0;
    width: 56px;
    height: 20px;
    text-align: right;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: @basic-grey;
    align-self: flex-end;
    cursor: pointer;
    transition: color @trans--300;

    &:hover {
      color: @basic-black;
    }
  }
}

.goods__list-buy-button {
  padding: 24px 216px 23px 217px;
  box-sizing: border-box;
  width: 505px;
  height: 70px;
  background-color: @basic-white;
  border: 1px solid @basic-black;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: @basic-black;
  cursor: pointer;
  transition: color @trans--300;
  transition: background-color @trans--300;

  &:hover {
    color: @basic-white;
    background-color: @basic-black;
  }

  &:active {
    color: @basic-white;
    background-color: @basic-black;
  }
}

.goods__list-total {
  display: none;
}

@media (max-width: @mobile-width) {
  .goods {
    h4 {
      font-size: 26px;
      line-height: 30px;
    }
  }
  
  .goods__list {
    display: flex;
    flex-direction: column;

    ul {
      margin-top: 32px;
    }
  
    li {
      align-self: center;
      min-height: 80px;
      min-width: 288px;
      padding-bottom: 16px;
      padding-right: 0;
      width: 100%;
      max-width: 100%;
  
      &:nth-of-type(n+2) {
        padding-top: 16px;
      }

      img {
        width: 80px;
        height: 80px;
      }
    }
  }

  .goods__list-description {
    margin-right: 16px;
  
    h5 {
      font-size: 16px;
      line-height: 18px;
    }
  
    p {
      display: none;
    }
  
    div {
      width: 81px;
      height: 34px;
  
      span {
        font-size: 16px;
        line-height: 18px;
      }
    }
  }

  .goods__list-description-button {
    width: 27px;
    height: 34px;
  }

  .goods__list-price {
    p {
      font-size: 16px;
      line-height: 18px;
      min-width: 73px;
    }
  
    button {
      width: 48px;
      height: 14px;
      font-size: 12px;
      line-height: 14px;
    }
  }
  
  .goods__list-buy-button {
    padding: 19px 108px 18px 108px;
    min-width: 288px;
    width: 100%;
    height: 60px;
    align-self: center;
    max-width: 505px;
  }

  .goods__list-total {
    display: flex;
    width: 100%;
    align-self: center;
    box-sizing: border-box;
    justify-content: space-between;
    margin-top: 24px;
    margin-bottom: 24px;
  
    p {
      font-weight: 700;
      font-size: 26px;
      line-height: 30px;
      color: @basic-black;
      margin: 0;
      align-self: center;
    }
  }
}